.cruises__article-container {
  display: flex;
  flex-direction: column;
  height: 100%;
  padding: 35px 25px 35px 25px;
}

.cruises__item:hover .cruises__article-container {
  align-items: flex-start;
}

.cruises__article-heading {
  position: static;
  margin: 0;
  margin-top: auto;
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
  color: #f9fbfd;
}

.cruises__item:hover .cruises__article-info {
  z-index: 0;
  display: flex;
  flex-direction: column;
  width: 100%;
}

.cruises__article-info {
  display: none;
  height: 100%;
  margin: 0;
  padding: 30px 0 30px 0;
  word-break: break-word;
}

.cruises__article-info dt {
  padding: 10px 0 0 0;
  font-weight: 500;
  font-size: 14px;
  line-height: 19.5px;
  color: #f9fbfd;
}

.cruises__article-info dd {
  margin: 0;
  font-weight: 400;
  font-size: 14px;
  line-height: 19.5px;
  color: #d8d8d9;
}

.cruises__item:hover .cruises__article-heading {
  position: absolute;
}

.cruises__article-container .cruises__article-booking-btn {
  display: none;
  justify-content: center;
  width: 100%;
  margin-top: auto;
  text-align: center;
}

.cruises__item:hover .cruises__article-booking-btn {
  display: block;
  z-index: 1;
}

.main-footer__wrapper {
  display: flex;
  align-items: center;
  max-width: 1366px;
  min-height: 138px;
  margin: 0 auto;
  padding: 0 103px;
  background-color: #011c40;
}
@media (max-width: 1023px) {
  .main-footer__wrapper {
    padding: 0 45px;
  }
}
@media (max-width: 767px) {
  .main-footer__wrapper {
    padding: 0 15px;
  }
}

.logo-footer {
  padding-right: 40px;
}

.logo-footer__link {
  width: 100%;
  height: 100%;
}

.logo-footer__image {
  fill: #f9fbfd;
}

.footer-nav__list {
  display: grid;
  grid-template-columns: 21% 19% 29% 16%;
  gap: 33px;
  width: 100%;
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (max-width: 1023px) {
  .footer-nav__list {
    grid-template-columns: 23% 20% 30% 15%;
    gap: 23px;
  }
}
@media (max-width: 767px) {
  .footer-nav__list {
    display: none;
  }
}

.social__list {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style: none;
}

.social__icon {
  fill: #f9fbfd;
}

.btn {
  display: inline-flex;
  padding: 10px 24px;
  font-weight: 600;
  font-size: 14px;
  line-height: 21px;
  color: #011c40;
  background-color: #f9fbfd;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(1, 28, 64, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 600px;
  padding: 40px;
  background-color: #f9fbfd;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__close-btn {
  position: absolute;
  top: 22px;
  right: 22px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

